import {
	isSameMonth
} from './public.js'

/**
 * @description 
 * @param {Bmob} bmob this.$bmob
 * @param {Number} timestamp 
 * @param {Boolean} isInReview 
 */
export const updateCycle = function(bmob, timestamp, isInReview) {
	return new Promise((resolve, reject) => {
		// 
		let flag = false
		// ID
		let objectId = ''
		
		const query = bmob.Query(!isInReview ? 'CSH_AllCycles' : 'CSH_AllCyclesTest');
		query.limit(10);
		query.order("-timestamp");
		uni.showLoading()
		query.find().then(res => {
			// 10
			for (let i = 0; i < res.length; i ++) {
				if (isSameMonth(new Date(res[i].timestamp))) {
					flag = true
					objectId = res[i].objectId
					break
				}
			}

			// 
			if (flag) {
				uni.hideLoading()
				uni.showModal({
					title: '',
					content: '',
					confirmText:'',
					cancelText:'',
					confirmColor:'#e96494',
					success(res) {
						if (res.confirm) {
							// 
							uni.showLoading()
							query.get(objectId).then(res => {
								uni.hideLoading()
								res.set('timestamp', timestamp)
								res.save()
								resolve(res)
							}).catch(err => {
								uni.hideLoading()
								reject(err)
							})
						}
					}
				})
				return
			}

			// 
			if (!flag) {
				// 
				query.set("timestamp", timestamp)
				query.save().then(res => {
					uni.hideLoading()
					resolve(res)
				}).catch(err => {
					uni.hideLoading()
					reject(err)
				})
			}
		});
	})
}

/**
 * @description 
 * @param {Bmob} bmob Bmob
 * @param {Number} page  
 * @param {Boolean} isInReview 
 */ 
export const allCycles = function(bmob, page, isInReview) {
	return new Promise((resolve, reject) => {
		const query = bmob.Query(!isInReview ? 'CSH_AllCycles' : 'CSH_AllCyclesTest');
		query.limit(1000)
		query.skip(page > 1 ? 1000*(page-1) : 0)
		query.find().then(res => {
		    resolve(res)
		}).catch(err => {
			reject(err)
		});
	})
}

/**
 * @description 
 * @param {Bmob} bmob 
 */
export const configs = function(bmob) {
	return new Promise((resolve, reject) => {
		const query = bmob.Query("CSH_Configs");
		query.find().then(res => {
		    resolve(res)
		}).catch(err => {
			reject(err)
		});
	})
}